:root {
  --accent-color: #00f0ff;
  --accent-color-hover: #17d4d4;
}

.about-section {
  min-height: 100vh;
  padding: 4.5rem 2rem;
  background: linear-gradient(to bottom, var(--intro-bg-top), var(--intro-bg-bottom));
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about-section::before {
  content: "";
  position: absolute;
  top: -30%;
  left: -30%;
  width: 180%;
  height: 180%;
  background: radial-gradient(circle at center, rgba(0, 240, 255, 0.12), transparent 70%);
  animation: fogPulse 30s infinite ease-in-out;
  z-index: 0;
  pointer-events: none;
}
[data-theme=light] .about-section::before {
  display: none;
}
.about-section > * {
  position: relative;
  z-index: 1;
}
.about-section .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
}
@media (max-width: 1024px) {
  .about-section .container {
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 6rem;
  }
}
.about-section .profile-img {
  flex-shrink: 0;
  width: 35rem;
  height: 35rem;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.1);
  transition: transform 0.5s ease;
  position: relative;
  margin: 0 auto;
  opacity: 0;
  animation: fadeInLeft 1s ease-out forwards;
  animation-play-state: paused;
}
.about-section .profile-img:hover {
  transform: scale(1.05);
}
.about-section .profile-img::after {
  content: "";
  position: absolute;
  bottom: -8%;
  left: 50%;
  transform: translateX(-50%);
  width: 120%;
  height: 40%;
  background: radial-gradient(circle, rgba(0, 240, 255, 0.06), transparent 70%);
  filter: blur(8px);
  z-index: -1;
}
@media (max-width: 1024px) {
  .about-section .profile-img {
    margin: 0;
    width: 30rem;
    height: 30rem;
  }
}
@media (max-width: 768px) {
  .about-section .profile-img {
    width: 26rem;
    height: 26rem;
    border-radius: 1.6rem;
    box-shadow: 0 0 6px rgba(255, 255, 255, 0.08);
  }
}
@media (max-width: 576px) {
  .about-section .profile-img {
    width: 22rem;
    height: 22rem;
    border-radius: 1rem;
    box-shadow: 0 0 4px rgba(255, 255, 255, 0.06);
  }
}
.about-section .text-content {
  flex: 1;
  color: var(--text-color);
  font-family: "Inter", sans-serif;
  text-align: center;
  margin-top: 3rem;
  opacity: 0;
  animation: fadeInUp 1s ease-out forwards;
  animation-delay: 0.4s;
  animation-play-state: paused;
}
@media (max-width: 1024px) {
  .about-section .text-content {
    text-align: left;
    margin-top: 0;
  }
}
.about-section .text-content .pre-title {
  font-size: 2.8rem;
  color: var(--accent-color);
  margin-bottom: 1.2rem;
}
.about-section .text-content .intro-lead {
  font-size: 3.8rem;
  font-weight: 600;
  margin-bottom: 1.2rem;
  color: var(--h2-color);
}
.about-section .text-content .intro-title {
  font-size: 3.2rem;
  font-weight: 800;
  color: var(--accent-color);
  text-shadow: 0 3px 6px black;
  margin-bottom: 1rem;
}
.about-section .text-content .tagline {
  font-size: 2rem;
  color: var(--accent-color);
  margin-bottom: 2.3rem;
}
.about-section .text-content .description {
  font-size: 2rem;
  max-width: 65ch;
  line-height: 1.7;
  color: var(--text-color);
}
[data-theme=light] .about-section {
  background: linear-gradient(to bottom, #D1D5DB, #D1D5DB);
}
[data-theme=light] .about-section .text-content .intro-title {
  color: var(--accent-color);
}
.about-section.in-view .profile-img {
  animation-play-state: running;
}
.about-section.in-view .text-content {
  animation-play-state: running;
}

@media (max-width: 576px) {
  .about-section {
    padding: 3rem 1.5rem;
    align-items: flex-start;
    min-height: initial;
  }
  .about-section .container {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 3rem;
  }
  .about-section .profile-img {
    width: 22rem;
    height: 22rem;
    border-radius: 1rem;
    box-shadow: 0 0 4px rgba(255, 255, 255, 0.06);
  }
  .about-section .text-content {
    margin-top: 2rem;
  }
  .about-section .text-content .pre-title {
    font-size: 1.8rem;
  }
  .about-section .text-content .intro-lead {
    font-size: 2.4rem;
  }
  .about-section .text-content .intro-title {
    font-size: 2.2rem;
    margin-top: 2rem;
  }
  .about-section .text-content .tagline {
    font-size: 1.6rem;
  }
  .about-section .text-content .description {
    font-size: 1.6rem;
    margin-top: 5.5rem;
  }
}
@keyframes fogPulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.15;
  }
}
@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-50px) scale(0.95);
  }
  80% {
    opacity: 1;
    transform: translateX(5px) scale(1.02);
  }
  100% {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(40px) scale(0.95);
  }
  80% {
    opacity: 1;
    transform: translateY(-5px) scale(1.02);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}